@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
@-webkit-keyframes bounce {
    0%, 20%, 60%, 100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }

    40%{
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    80%{
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }
}
@keyframes bounce {
    0%, 20%, 60%, 100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }

    40%{
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    80%{
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }
}
*{
    text-decoration: none;
    margin: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
    list-style: none;
}
html{
    font-size: 62.5%;
}
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}   
.container{
    width: 90vw;
}

.blue-titles{
    display: flex;
    font-size: 3rem;
    font-family: 'Crimson Text';
    color: #0F214F;
    font-weight: 400;
}
.grey-text{
    font-family: 'Nunito', sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    color: #3D3D3D;
}

.main-title{
    font-family: 'Nunito', sans-serif;
    font-size: 3.2rem;
    align-self: center;
    font-weight: 700;
    color: #FBB667;
}

.main-title > span{
    color:#0F214F;
}
.icon {
    position: absolute;
    top: 4rem;
    left: 85%;
    z-index: 4;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 4rem;
    cursor: pointer;
}
.burguer{
    left: 1%;
    width: 3.6rem;
    height: .6rem;
    background: black;
    position: absolute;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
}

.burguer::before{
    top: -12px;
    content: '';
    position: absolute;
    width: 3.6rem;
    height: 6px;
    background-color: black;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
}

.burguer:after{
    top: 12px;
    content: '';
    position: absolute;
    width: 3.6rem;
    height: 6px;
    background-color: black;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
}

.list{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4.5rem 0 0;
    flex-wrap: nowrap;
    color: black;
    font-size: 1.2rem;
    font-weight: 600;
    grid-gap: 5rem;
    gap: 5rem;
}

.list .listItems{
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 8rem;
    height: 100%;
    grid-gap: 6rem;
    gap: 6rem;
}

.list .listItems li{
    margin: .2rem .8rem;
    cursor: pointer;
}

.list .listItems li:hover{
    font-weight: 700;
}

.icon.iconActive .burguer{
    background-color: transparent;
    box-shadow: 0 2px 5px transparent;
}

.icon.iconActive .burguer:after {
    top: 0;
    background-color: black;
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

.icon.iconActive .burguer::before {
    top: 0;
    background-color: black;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

.menu.menuOpen{
    width: 100%;
    height: 100vh;
    background-color: white; 
}

.menu.menuClose{
    display: none;
    width: 0px;
}
.Header{
    position: fixed;
    top: 0;
    width: 100vw;
    padding: 1.6rem calc(10vw);
    background-color: white;
}   
.Header > ul{
    display: none;
}
.Header > button {
    display: none;
}
.Header a{
    font-size: 2.4rem;
    font-weight: 700;
    width: 50px;
}

.book-a-tour-btn{
    background-color: #FBB667;
    box-shadow: 0px 5px 8px rgba(251, 182, 103, 0.58);
    border-radius: 10rem;
    font-family: 'Nunito';
    font-size: 2rem;
    font-weight: 700;
    color: white;
    padding: 1rem 3rem;
}
@media (min-width: 768px){
    .menu-dropdown{
        display: none;
    }
    .Header{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .Header > ul{
        display: flex;
        grid-gap: 3rem;
        gap: 3rem;
    }
}
@media (min-width: 1024px){
    .Header > button {
        display: flex;
        cursor: pointer;
    }
    .Header > button:hover{
        -webkit-animation: bounce 1s;
                animation: bounce 1s;
    }
}
.Discover{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-top: 12rem;
    grid-gap: 2rem;
    gap: 2rem;
    background-color: white;
}
.Discover-Info{
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem 1rem;
    max-width: 100%;
}
.Discover-Info > div {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    max-width: 100%;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.21);
    border-radius: 10rem;
    padding: 1.1rem 2.5rem;
}
.Discover-Info > div > input{
    font-family: 'Nunito', sans-serif;
    font-size: 1.5rem;
    color: #FBB667;
    border: none; 
    width: calc(100% - 4rem);
    background: #FFFFFF;
}
input::-webkit-input-placeholder{
    color:#FBB667;
}
input:-ms-input-placeholder{
    color:#FBB667;
}
input::placeholder{
    color:#FBB667;
}
.Discover-Info > div > button{
    font-family: 'Nunito', sans-serif;
    font-weight: 700;
    color: white;
    background-color: #FBB667;
    font-size: 1.4rem;
    border-radius: 10rem;
    padding: .3rem 1rem; 
    border: none;
    cursor: pointer;
}
.Discover-img{
    max-width: 80vw;
}
@media (min-width: 768px){
    .Discover{
        flex-direction: row;
        justify-content: space-between;
        padding: 12rem 0rem 2rem 0rem;
    }
    .Discover-Info > h2{
        font-size: 7.6rem;
    }
    .Discover-Info > p{
        font-size: 1.8rem;
    }
    .Discover-Info > div > button:hover{
        -webkit-animation: bounce 1s;
                animation: bounce 1s;
    }
    .Discover-img{
        max-width: 45vw;
    } 
}
.Destination{
    padding: 4rem 0rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 4rem;
    gap: 4rem;
}
.Destination-img{
    width: 70vw;
}
.Destination-Info{
    max-width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem 1rem;
}
.Destination-Info > h2{
    color: black;
}
.Company-status{
    display: none;
}
.Destination-Info > a{
    color: white;
    font-family: 'Nunito', sans-serif;
    background-color: #0F214F;
    padding: 1rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 10rem;
    width: -webkit-max-content;
    width: max-content;
}
@media (min-width: 768px) {
    .Destination{
        flex-direction: row;
        justify-content: space-between;
        padding: 4rem 0rem;
    }
    .Destination-Info > h2{
        font-size: 5.2rem;
    }
    .Destination-Info > a:hover{
        -webkit-animation: bounce 1s;
                animation: bounce 1s;
    }
}

@media (min-width: 900px){
    .Company-status{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        grid-gap: 7rem;
        gap: 7rem;
    }
    .Company-status > p{
        display: flex;
        flex-wrap: wrap;
        max-width: 8rem;
        font-family: 'Nunito', sans-serif;
        font-weight: 300;
        font-size: 1.6rem;
        color: black;
    }
    .Company-status > p > span{
        color: #FBB667;
        font-weight: 700;
        font-family: 'Nunito', sans-serif;
        font-size: 4.4rem;
    }
}
.Destination-Cards{
    display: flex;
    align-items: center;
    overflow-x: auto;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
}
.Destination-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}
.Destination-place-img{
    width: 70vw;
}
.Destination-card > h2{
    font-size: 1.8rem;
}
.Destination-card > p{
    color: #0F214F;
    font-family: 'Nunito', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
}
@media (min-width: 650px){
    .Destination-Cards{
        justify-content: space-evenly;
    }
    .Destination-card > h2 {
        font-size: 1.8rem;
    }
    .Destination-card > p {
        font-size: 1.4rem;
    }
    .Destination-place-img{
        width: 25vw;
    }
}
@media (min-width: 768px){  
    .Destination-card > h2{
        font-size: 2rem;
    }
    .Destination-card > p{
        font-size: 2rem;
    }
}

@media (min-width: 1200px) {
    .Destination-card > h2 {
        font-size: 2.5rem;
    }
}
.About-us{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    padding: 4rem 0rem;
    grid-gap: 2rem;
    gap: 2rem;
}
.van-img{
    width: 80vw;
}
.About-us-Info{
    display: flex;
    flex-direction: column;
    grid-gap: 3rem;
    gap: 3rem;
}
.About-us-Info > h2{
    font-family: 'Crimsom Text';
    font-weight: 400;
    font-size: 3rem;
    color: black;
    padding: 3rem 0rem;
}
.Services{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}
.Service-img{
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}
.Services > div{
    display: flex;
    flex-direction: column;
    grid-gap: .4rem;
    gap: .4rem;
}
.Services h3{
    font-family: 'Crimsom Text';
    font-weight: 400;
    font-size: 2rem;
    color: black;
}
.Services p{
    font-size: 1.5rem;
}

@media (min-width: 768px){
    .About-us{
        flex-direction: row;
        grid-gap: 4rem;
        gap: 4rem;
    }
    .van-img{
        width: 45vw;
    }
    .About-us-Info > h2{
        font-size: 4rem;
    }
    .Services h3{
        font-size: 2.5rem;
    }
}
.Footer-all{
    background-color: rgba(174, 203, 244, 0.13);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.footer-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 5rem;
    gap: 5rem;
    padding: 3rem 0rem;
}
.Company-info{
    width: 25vw;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}
.Company-info > h1{
    font-family: 'Nunito', sans-serif;
    font-size: 3.2rem;
    align-self: center;
    font-weight: 700;
    color: #FBB667;
}
.Company-info > h1 > span {
    color: #0F214F;
}
.Company-info > p{
    display: none;
}
.footer-links{
    display: flex;
    width: 100%;
    justify-content: space-around;
    grid-gap: 2rem;
    gap: 2rem;
}
.Links{
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    width: 100px;
}
.Links > h2{
    font-family: 'Nunito', sans-serif;
    font-weight: 700;
    font-size: 2rem;
    padding-bottom: 1rem;
}
.Links > p{
    font-family: 'Nunito', sans-serif;
    font-size: 1.5rem;
    font-weight: 300;
}
.Links a{
    color: black;
}
.footer-date{
    display: flex;
    justify-content: center;
    background-color: #FBB667;
    padding: 2rem;
    font-family: 'Nunito', sans-serif;
    color: white;
    font-weight: 400;
    font-size: 1.5rem;
    width: 100%;
}
@media (min-width: 768px){   
    .footer-info{
        flex-direction: row;
        align-items: baseline;
    }
    .Company-info > h1{
       align-self: flex-start;
    }
    .Company-info > p{
        display: flex;
    }
    .Links a:hover{
        font-weight: 700;
    }
}
